/* Signup.module.css */
.signupForm {
    display: flex;
    flex-direction: column;
    max-width: 300px;
    margin: auto;
}

.inputContainer {
    margin-bottom: 10px;
}

.label {
    margin-bottom: 5px;
    font-size: 16px;
}

.input {
    padding: 8px;
    font-size: 14px;
}

.button {
    padding: 10px;
    font-size: 16px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
